.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #333;
  color: white;
}

.navbar-links {
  display: flex;
}

.navbar-links a {
  color: white;
  text-decoration: none;
  margin-right: 10px;
}

.navbar-links a:last-child {
  margin-right: 0;
}

.navbar-links a:hover {
  text-decoration: underline;
}
